import {
  HTMLAttributes,
  useEffect,
  useState,
  useCallback,
  useMemo,
} from 'preact/compat';

import {
  Container,
  toLanguageTag,
  SlotProps,
  SlotMethod,
  Slot,
  ResolveImageUrlOptions,
} from '@adobe/elsie/lib';

import {
  Button,
  Icon,
  ButtonProps,
  Image,
  Incrementer,
  Breadcrumbs,
} from '@adobe/elsie/components';
import { Product, Swatches, PriceRange, Option } from '@/pdp/components';
import { config, getProductData, getRefinedProduct } from '@/pdp/api';
import { events } from '@adobe/event-bus';
import { Lang, useText } from '@adobe/elsie/i18n';
import { isDateValid, toDateString } from '@/pdp/lib/date';
import { getUrlParams, setUrlParams } from '@/pdp/lib/url-params';
import { ProductModel } from '@/pdp/data/models';

type IconType = keyof typeof import('@adobe/elsie/icons');

type Values = {
  sku: string;
  quantity: number;
  optionsUIDs?: string[];
};

type DefaultSlotContext = {
  data: ProductModel | null;
  values: Values;
  valid: boolean;
};

export type CarouselConfig = {
  mobile?: boolean;
  controls?: 'thumbnailsRow' | 'thumbnailsColumn' | 'dots' | null;
  arrowsOnMainImage?: boolean;
  loopable?: boolean;
  peak?: {
    mobile: boolean,
    desktop: boolean,
  }
  gap?: 'small' | 'medium' | 'large' | null;
  thumbnailsLoadingMode?: 'lazy' | 'eager';
  imageParams?: ResolveImageUrlOptions,
  thumbnailParams?: ResolveImageUrlOptions
};

export type OptionsConfig = {
  anchorOptions?: string[];
};

export interface ProductDetailsProps extends HTMLAttributes<HTMLDivElement> {
  sku: string;
  hideSku?: boolean;
  productData?: ProductModel;
  hideQuantity?: boolean;
  hideShortDescription?: boolean;
  hideDescription?: boolean;
  hideAttributes?: boolean;
  hideURLParams?: boolean;
  hideSelectedOptionValue?: boolean;
  slots?: {
    Title?: SlotProps<DefaultSlotContext>;

    SKU?: SlotProps<DefaultSlotContext>;

    RegularPrice?: SlotProps<DefaultSlotContext>;

    SpecialPrice?: SlotProps<DefaultSlotContext>;

    Options?: SlotProps<DefaultSlotContext>;

    Quantity?: SlotProps<DefaultSlotContext>;

    Actions?: SlotProps<DefaultSlotContext & {
      appendButton: SlotMethod<
        Omit<ButtonProps, 'icon'> & {
          text?: string;
          icon?: IconType;
        }
      >;
    }>;

    ShortDescription?: SlotProps<DefaultSlotContext>;

    Description?: SlotProps<DefaultSlotContext>;

    Attributes?: SlotProps<DefaultSlotContext>;

    Breadcrumbs?: SlotProps<DefaultSlotContext & {
      setSeparator: SlotMethod<IconType>;

      appendLink: SlotMethod<
        HTMLAttributes<HTMLAnchorElement> & { text?: string }
      >;

      appendHTMLElement: SlotMethod<HTMLElement>;
    }>;

    GalleryContent?: SlotProps<DefaultSlotContext>;

    InfoContent?: SlotProps<DefaultSlotContext>;

    Content?: SlotProps<DefaultSlotContext>;
  };
  carousel?: CarouselConfig;
  optionsConfig?: OptionsConfig;
  onAddToCart?: (values: Values) => void;
}

export const ProductDetails: Container<
  ProductDetailsProps,
  ProductModel | null
> = ({
  sku,
  hideSku,
  hideQuantity,
  hideShortDescription,
  hideDescription,
  hideAttributes,
  hideURLParams,
  hideSelectedOptionValue,
  slots,
  children,
  initialData,
  carousel,
  optionsConfig,
  onAddToCart,
  ...props
}) => {
  // i18n
  const AddToCartLabel = useText('PDP.Product.AddToCart.label').label;
  const IncrementerLabel = useText('PDP.Product.Incrementer.label').label;
  const DetailsLabel = useText('PDP.Product.Details.label').label;

  // TODO: Investigate if the "|| 'en-US' " is really necessary
  const [locale, setLocale] = useState<Lang>(config.getConfig().defaultLocale as Lang || 'en-US');

  const [selectionsToUpdate, setSelectionsToUpdate] = useState<Option[]>();

  const [data = null, setData] = useState(initialData);

  const [values, setValues] = useState<Values>(() => {
    const defaultValues: Values = { sku, quantity: 1 };

    if (data?.optionUIDs) {
      defaultValues.optionsUIDs = data.optionUIDs;
    }
    return defaultValues;
  });

  // the context is invalid for a configurable product that doesn't have all the options selected
  const [valid, setValid] = useState(() => {
    return !(
      data?.options &&
      data?.options?.length > 0 &&
      data?.optionUIDs?.length !== data?.options.length
    );
  });

  const defaultSlotContext: DefaultSlotContext = { data, values, valid };

  // Locale
  useEffect(() => {
    const localeEvent = events.on('locale', (payload: string) => {
      if (payload === locale) return;

      setLocale(payload as Lang);

      getProductData(sku).then(setData);

      // TODO: need to keep the optionsUIDs when locale changes
      // if (values?.optionsUIDs) {
      //   getRefinedProduct(sku, values?.optionsUIDs).then(setData);
      // } else {
      //   getProductData(sku).then(setData);
      // }
    });
    return () => {
      localeEvent?.off();
    };
  }, [locale, sku]);

  // Callbacks
  const handleOnValues = useCallback(
    (newValues: { [key: string]: string | number }) => {
      setValues((prev) => ({ ...prev, ...newValues }));
    },
    []
  );

  const handleOptionSelected = useCallback(
    (selections: { [id: string]: { label: string; value: string } }) => {
      // options Selected
      let optionsUIDs = Object.keys(selections)
        .map((id) => selections[id].value)
        .filter((element) => element !== undefined);

      // set optionUIDs to URL params
      // ?optionsUIDs=1,2,3
      if (!hideURLParams) {
        setUrlParams({ optionsUIDs: optionsUIDs.join(',') });
      }

      // update state
      setValues((prev) => ({ ...prev, optionsUIDs }));
      setValid(optionsUIDs?.length === data?.options?.length);

      // get refined product data and update state
      getRefinedProduct(sku, optionsUIDs, optionsConfig?.anchorOptions).then((next) => {
        setData(next);

        // special processing for anchor options (options that should not be refined when other options are selected)
        if (optionsConfig?.anchorOptions?.length) {
          processUpdates(next, optionsUIDs);
        }
      });
    },
    [sku]
  );

  const processUpdates = useCallback((next: ProductModel | null, previousOptionsUIDs: string[]) => {
    const updatedOptionsUIDs = next?.optionUIDs ?? [];
    const haveSameValues = previousOptionsUIDs.every(item => updatedOptionsUIDs.includes(item));
    if (!haveSameValues) {
      setSelectionsToUpdate(next?.options);
      setValues((prev) => ({ ...prev, optionsUIDs: updatedOptionsUIDs }));
      setValid(updatedOptionsUIDs?.length === next?.options?.length);
    }
  }, [sku]);

  const handleOnErrors = useCallback((_errors: { [id: string]: string }) => {
    if (Object.keys(_errors).length !== 0) setValid(false);
  }, []);

  // Product Attributes
  const attributes = useMemo(() => {
    return data?.attributes?.map(({ label, value }, key: number) => {
      const __html = isDateValid(value)
        ? toDateString(value, locale)
        : value.toString();

      return (
        <li key={key}>
          {/* eslint-disable-next-line react/no-danger */}
          {label}: <span dangerouslySetInnerHTML={{ __html }} />
        </li>
      );
    });
  }, [data?.attributes, locale]);

  const quantity =
    !hideQuantity && data?.inStock ? (
      <Slot
        name="Quantity"
        slot={slots?.Quantity}
        context={{ ...defaultSlotContext }}
      >
        <Incrementer
          key="quantity"
          name="quantity"
          defaultValue={values.quantity.toString()}
          min={1}
          aria-label={IncrementerLabel}
          className="pdp-product__quantity"
          onValue={(quantity) => {
            handleOnValues({ quantity: Number(quantity) });
          }}
        />
      </Slot>
    ) : undefined;

  const renderOptions = () => {
    if (data?.options) {
      return (
        <Slot name="Options" slot={slots?.Options} context={{ ...defaultSlotContext }}>
          <Swatches
            options={data.options}
            defaultOptions={values.optionsUIDs}
            selectionsToUpdate={selectionsToUpdate}
            hideSelectedValue={hideSelectedOptionValue}
            onValues={handleOptionSelected}
            onErrors={handleOnErrors}
          />
        </Slot>
      );
    }
  };

  return (
    <div {...props}>
      <Product
        title={
          <Slot
            name="Title"
            slot={slots?.Title}
            context={{ ...defaultSlotContext }}
          >
            {data?.name}
          </Slot>
        }
        sku={
          hideSku ? undefined : (
            <Slot
              name="SKU"
              slot={slots?.SKU}
              context={{ ...defaultSlotContext }}
            >
              {data?.sku}
            </Slot>
          )
        }
        options={renderOptions()}
        breadcrumbs={
          slots?.Breadcrumbs && (
            <Slot
              name="Breadcrumbs"
              slot={slots?.Breadcrumbs}
              context={{
                ...defaultSlotContext,

                setSeparator(callback) {
                  this._registerMethod((...attrs) => {
                    const nextSeparator = callback(...attrs);

                    const separator = nextSeparator && <Icon source={nextSeparator} />;

                    this._setProps((prev: any) => {
                      return { ...prev, separator };
                    });
                  });
                },

                appendLink(callback) {
                  this._registerMethod((...attrs) => {
                    const { text, ...p } = callback(...attrs);

                    const link = <a {...p}>{text}</a>;

                    this._setProps((prev: any) => {
                      return {
                        ...prev,
                        categories: [...(prev.categories || []), link],
                      };
                    });
                  });
                },

                appendHTMLElement(callback) {
                  this._registerMethod((...attrs) => {
                    const domElement = callback(...attrs);

                    const vnode = this._htmlElementToVNode(domElement);

                    this._setProps((prev: any) => {
                      return {
                        ...prev,
                        categories: [...(prev.categories || []), vnode],
                      };
                    });
                  });
                },
              }}
              render={({ separator, categories }) => {
                return (
                  <Breadcrumbs separator={separator} categories={categories} />
                );
              }}
            />
          )
        }
        quantity={quantity}
        actions={
          <Slot
            name="Actions"
            slot={slots?.Actions}
            context={{
              ...defaultSlotContext,

              appendButton(callback) {
                this._registerMethod((...attrs) => {
                  const _button = callback(...attrs);

                  if (!_button) return;

                  const { text, icon, ...buttonProps } = _button;

                  const button = (
                    <Button
                      type="button"
                      {...buttonProps}
                      icon={icon && <Icon source={icon} />}
                    >
                      {text}
                    </Button>
                  );

                  this._setProps((prev: any) => ({
                    children: [...(prev.children || []), button],
                  }));
                });
              },
            }}
          >
            {
              // Default Add to Cart button if no slot is provided
              !slots?.Actions && (
                <Button
                  size="medium"
                  type="submit"
                  icon={<Icon source="Cart" />}
                  disabled={!data?.inStock || !valid}
                  aria-label={AddToCartLabel}
                  onClick={() => {
                    return onAddToCart?.(values);
                  }}
                >
                  {AddToCartLabel}
                </Button>
              )
            }
          </Slot>
        }
        shortDescription={
          !hideShortDescription ? (
            <Slot
              name="ShortDescription"
              slot={slots?.ShortDescription}
              context={{ ...defaultSlotContext }}
            >
              {/* eslint-disable-next-line react/no-danger */}
              <div dangerouslySetInnerHTML={{ __html: data?.shortDescription ?? '' }} />
            </Slot>
          ) : undefined
        }
        description={
          !hideDescription ? (
            <Slot
              name="Description"
              slot={slots?.Description}
              context={{ ...defaultSlotContext }}
            >
              {/* eslint-disable-next-line react/no-danger */}
              <div dangerouslySetInnerHTML={{ __html: data?.description ?? '' }} />
            </Slot>
          ) : undefined
        }
        images={
          data?.images?.map(({ label, url }) => (
            <Image
              key={url}
              title={label}
              alt={label}
              src={url}
              // TODO: change width/height for image aspect ratio
              // pending https://jira.corp.adobe.com/browse/DATA-5122
              width={carousel?.imageParams?.width ?? 960}
              height={carousel?.imageParams?.height ?? 1191}
              params={carousel?.imageParams ?? { width: 960 }}
            />
          )) ?? []
        }
        specialPrice={
          data?.prices?.visible ? (
            <Slot
              name="SpecialPrice"
              slot={slots?.SpecialPrice}
              context={{ ...defaultSlotContext }}
            >
              <PriceRange {...data.prices.final} locale={toLanguageTag(locale)} />
            </Slot>
          ) : undefined
        }
        price={
          data?.prices?.visible &&
          data.prices.regular &&
          data.prices.final.amount !== data?.prices.regular.amount ? (
            <Slot
              name="RegularPrice"
              slot={slots?.RegularPrice}
              context={{ ...defaultSlotContext }}
            >
              <PriceRange
                {...data.prices.regular}
                locale={toLanguageTag(locale)}
              />
            </Slot>
          ) : undefined
        }
        carouselConfig={{
          ...carousel,
          thumbnails: carousel
            ? data?.images?.map(({ label, url }) => (
                <Image
                  key={url}
                  title={label}
                  alt={label}
                  src={url}
                  // TODO: change width/height for image aspect ratio
                  // pending https://jira.corp.adobe.com/browse/DATA-5122
                  width={carousel?.thumbnailParams?.width ?? 200}
                  height={carousel?.thumbnailParams?.height ?? 248}
                  params={carousel?.thumbnailParams ?? { width: 200 }}
                />
              )) ?? []
            : [],
        }}
        outOfStock={!data?.inStock}
        attributes={
          !hideAttributes ? (
            <Slot
              name="Attributes"
              slot={slots?.Attributes}
              context={{ ...defaultSlotContext }}
            >
              {attributes?.length ? (
                <>
                  {DetailsLabel}
                  <ul>{attributes}</ul>
                </>
              ) : null}
            </Slot>
          ) : undefined
        }
        galleryContent={
          slots?.GalleryContent && (
            <Slot
              name="GalleryContent"
              slot={slots.GalleryContent}
              context={{ ...defaultSlotContext }}
            />
          )
        }
        infoContent={
          slots?.InfoContent && (
            <Slot
              name="InfoContent"
              slot={slots.InfoContent}
              context={{ ...defaultSlotContext }}
            />
          )
        }
        productContent={
          slots?.Content && (
            <Slot
              name="Content"
              slot={slots.Content}
              context={{ ...defaultSlotContext }}
            />
          )
        }
      />
    </div>
  );
};

ProductDetails.getInitialData = async function ({ sku, optionsConfig }) {
  // check if default selections are made
  const defaultSelections = getUrlParams().optionsUIDs?.split(',') || config?.getConfig()?.models?.ProductDetails?.initialData?.optionsUIDs;

  // if it has selections, refine the product data
  if (defaultSelections?.length) {
    return await getRefinedProduct(sku, defaultSelections, optionsConfig?.anchorOptions);
  }

  // otherwise return the product data
  return await getProductData(sku);
};
